import React from "react";
import Table from "react-bootstrap/Table";
import "./ErrorPopup.css";
import ReusableAlert from "../Alert/ReusableAlert";

const ErrorPopup = ({ errors }) => {
  console.log("Errors in ErrorPopup:", errors);

  // Group errors by row number
  const groupedErrors = new Map();

  errors.forEach((error) => {
    // Extract row number and column name using regex
    const rowMatch = error.match(/Row (\d+):/);
    const columnMatch = error.match(/"([^"]*)" is required/);

    const rowNum = rowMatch ? rowMatch[1] : "-"; // Get row number or default to "-"
    const columnName = columnMatch ? columnMatch[1] : "-"; // Get column name or default to "-"
    const errorMsg = rowMatch ? error.split(": ")[1] : error; // Extract error message

    // Initialize array for errors if row number is not already in the Map
    if (!groupedErrors.has(rowNum)) {
      groupedErrors.set(rowNum, []);
    }

    // Add error details to the groupedErrors Map
    groupedErrors.get(rowNum).push({ errorMsg, columnName });
  });

  const errorMessage = (
    <>
      <strong>‚ö†Ô∏è Note:</strong> Below reports show only uploaded spreadsheet
      error rows. To resolve these issues, update the spreadsheet and re-import.
    </>
  );

  return (
    <>
      <ReusableAlert
        variant="danger"
        message={errorMessage}
        className="custom-alert"
      />
      <div className="error-table-container">
        <Table responsive bordered className="custom-error-table">
          <thead>
            <tr>
              <th>Row Number</th>
              <th>Error Description</th>
              <th>Column Name</th>
            </tr>
          </thead>
          <tbody>
            {Array.from(groupedErrors.entries()).length > 0 ? (
              Array.from(groupedErrors.entries()).map(
                ([rowNum, errorDetails]) =>
                  errorDetails.map(({ errorMsg, columnName }, index) => (
                    <tr key={`${rowNum}-${index}`}>
                      <td className="row-number">{rowNum}</td>
                      <td className="error-description">{errorMsg}</td>
                      <td className="column-name">{columnName}</td>
                    </tr>
                  ))
              )
            ) : (
              <tr>
                <td colSpan={3} className="text-center no-errors">
                  No Errors Found
                </td>
              </tr>
            )}
          </tbody>
        </Table>
      </div>
    </>
  );
};

export default ErrorPopup;
